import React from 'react';
//import PropTypes from 'prop-types';
import axios from 'axios';
import Movie from './movies';
import './App.css';


class App extends React.Component{
  state={
    isLoading:true,
    movies:[]
  };
  getMovies = async ()=>{ // async 비동기, 기다려라
    const {data:{data:{movies}}}= await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating"); // await인 함수가 실행이 다 될때까지 기다려라
    this.setState({movies,isLoading:false}) // same as movies(state):movies(axios)
  }
  componentDidMount(){
    this.getMovies();
  }
  render(){ // 배열 컴포넌트도 안에 하나하나씩 랜더링한다
    const {isLoading,movies}=this.state;
    return <section className="container">
      {isLoading ? (<div className="loader"><span className="loader_text">
        Loading...</span>
        </div>)
        :(<div className="movies">
          {movies.map(movie => (<Movie key={movie.id} 
          id={movie.id} 
          year={movie.year} 
          title={movie.title} 
          summary={movie.summary} 
          poster={movie.medium_cover_image}
          genres={movie.genres}
          />))}
          </div>) 
    }</section>;
  
  }
}

export default App;
